import { Canvas, Circle, Group, useTiming } from '@shopify/react-native-skia';
import React from 'react';
import { TouchableOpacity } from 'react-native';

interface Props {
  activePosition: number; // 0, 1 or 2
  onPress: () => void;
  type?: 'two-way' | 'three-way';
}

const SIZE = 50;
const RADIUS = 10;
const X_DISTANCE = 6;

const Switch = ({ activePosition, type = 'three-way', onPress }: Props) => {
  const switchX = useTiming(
    SIZE / 2 -
      X_DISTANCE +
      (type === 'three-way' ? activePosition : activePosition === 0 ? 0 : 2) * X_DISTANCE,
    { duration: 300 }
  );
  return (
    <TouchableOpacity onPress={onPress} activeOpacity={1}>
      <Canvas style={{ width: SIZE, height: SIZE }} /* onTouch={touchHandler} */>
        <Group>
          <Circle r={RADIUS} cx={SIZE / 2} cy={SIZE / 2} color={'#eee'}>
            {/* <Shadow dx={0} dy={10} blur={2} color="rgba(0,0,0,0.2)" inner /> */}
          </Circle>
          <Circle r={RADIUS - 2} cx={switchX} cy={SIZE / 2} color={'#666'} />
        </Group>
      </Canvas>
    </TouchableOpacity>
  );
};

export default Switch;
