import React, { PropsWithChildren } from 'react';
import { View } from 'react-native';
import useDeviceSize, { DEVICE_SIZES } from '../hooks/useDeviceSize';
import useTheme from '../hooks/useTheme';

export default function ProjectsContainer({ children }: PropsWithChildren<{}>) {
  const theme = useTheme();
  const { width, size } = useDeviceSize();
  return (
    <View
      style={{
        flexDirection: width < 800 ? 'column' : 'row',
        flex: '100%',
        flexWrap: 'wrap',

        gap: size >= DEVICE_SIZES.s ? theme.space.xl : theme.space.l,
      }}
    >
      {children}
    </View>
  );
}
