interface Props {}

const Enclosure = ({ children }: PropsWithChildren<Props>) => {
  const theme = useTheme();
  const { width } = useDeviceSize();
  const orientation = useOrientation();

  const r = (width - 32) / 2;

  let WIDTH = 388;
  let HEIGHT = 716;

  if (WIDTH > width) {
    const diff = width / WIDTH;
    WIDTH *= diff;
    HEIGHT *= diff;
  }

  if (orientation === 'LANDSCAPE') {
    return (
      <View
        style={{
          flex: 1,
          backgroundColor: 'black',
        }}
      >
        {children}
      </View>
    );
  }

  return (
    <View
      style={{
        flex: 1,
        backgroundColor: 'black',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <View
        style={{
          height: HEIGHT,
          width: WIDTH,
        }}
      >
        <Canvas style={{ position: 'absolute', width: WIDTH, height: HEIGHT }}>
          <Box box={rrect(rect(0, 0, WIDTH, HEIGHT - 1), 24, 24)} color={theme.colors.enclosure}>
            <BoxShadow dx={6} dy={6} blur={8} color="#666" inner />
            <BlurMask blur={20} style="inner" />
          </Box>
        </Canvas>
        {children}
      </View>
    </View>
  );
};

export default Enclosure;
