import { useOrientation } from '../../../app/hooks/useOrientation';
import useTheme from '../../../app/theme/useTheme';
import {
  Canvas,
  Circle,
  Group,
  Path1DPathEffect,
  useTouchHandler,
  useValue,
  useComputedValue,
  Rect,
  runSpring,
  RoundedRect,
  Shadow,
} from '@shopify/react-native-skia';
import React, { MutableRefObject, Ref, useCallback, useEffect, useImperativeHandle } from 'react';
import { Text, View } from 'react-native';
import { runOnJS } from 'react-native-reanimated';
import Sound from 'react-native-sound';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import useDeviceSize from '../../../../../../hooks/useDeviceSize';

const TOTAL_ROTATION = 270;

export interface KnobRefType {
  reset: () => void;
  rotateTo: (value: number) => void;
}
export interface KnobItem {
  id: number;
  label: string;
  sound?: Sound;
  maxVolume?: number;
  ref?: MutableRefObject<KnobRefType | undefined>;
}

interface Props {
  item: KnobItem;
  size: number;
  margin?: number;
  onRotate?: (value: number) => void; // value is 0 to 1
  type?: 'white' | 'black';
}

const START_ROTATE = -140 * 1.16; // 0 to TOTAL_ROTATION. 1.16 is a magic number that makes the value 'degrees'.

const Knob = React.forwardRef<any, Props>(
  ({ item, size, margin = 0, onRotate, type = 'white' }: Props, ref: Ref<KnobRefType>) => {
    const { sound, label, maxVolume = 1 } = item;
    const { height: screenHeight } = useDeviceSize();
    const theme = useTheme();
    const orientation = useOrientation();
    const rotate = useValue(START_ROTATE);
    const insets = useSafeAreaInsets();
    const sliderHeight = screenHeight - insets.top - insets.bottom - 40;
    const sliderTop = insets.top;

    const knobPointerWidth = size * 0.1;
    const knobRadius = size * 0.5 - 10;

    const setVolume = (volume: number) => {
      if (!sound) return;
      if (volume < 0) volume = 0;
      if (volume > 1) volume = 1;
      const newVolume = Math.round(volume * 100) / 100;
      sound.setVolume(newVolume * maxVolume);
    };

    useImperativeHandle(ref, () => ({
      reset() {
        runSpring(rotate, START_ROTATE);
        play();
      },
      rotateTo(value) {
        runSpring(rotate, value - START_ROTATE);
      },
    }));

    const play = useCallback(
      (secondAttempt = false) => {
        if (!sound) return;
        sound.setVolume(0);
        sound.setNumberOfLoops(-1);

        if (sound.isPlaying()) {
          sound.stop();
        }

        sound.play((success: boolean) => {
          if (success) {
          } else {
            if (!secondAttempt) {
              setTimeout(() => {
                play(true);
              }, 2000);
            }
          }
        });
      },
      [sound]
    );

    useEffect(() => {
      const hack = setTimeout(() => {
        play();
        return () => clearTimeout(hack);
      }, 200);
    }, [play]);

    const isDisabled = !sound && !onRotate;

    const onStartRotateValue = useValue(0);
    const onStartY = useValue(0);

    const touchHandler = useTouchHandler({
      onStart: (touchInfo) => {
        onStartRotateValue.current = rotate.current;
        onStartY.current = touchInfo.y;
      },
      onActive: (touchInfo) => {
        let newRotate = onStartRotateValue.current - touchInfo.y + onStartY.current;
        if (newRotate < START_ROTATE) newRotate = START_ROTATE;
        const max = TOTAL_ROTATION + START_ROTATE + 60;
        if (newRotate > max) newRotate = max;
        rotate.current = newRotate;
        runOnJS(setVolume)((newRotate - START_ROTATE) / TOTAL_ROTATION);
        !!onRotate && runOnJS(onRotate)((newRotate - START_ROTATE) / TOTAL_ROTATION);
      },
    });

    const transform = useComputedValue(
      () => [
        {
          rotate: rotate.current * 0.015,
        },
      ],
      [rotate]
    );
    const transformSlider = useComputedValue(
      () => [
        {
          translateY: -rotate.current * 0.6 - sliderHeight * 0.35,
        },
      ],
      [rotate]
    );
    /* const transformInvert = useComputedValue(
      () => [
        {
          rotate: -rotate.current * 0.015,
        },
      ],
      [rotate]
    ); */
    const origin = useComputedValue(
      () => ({
        x: size / 2,
        y: size / 2,
      }),
      [size]
    );

    const faderKnobWidth = size * 0.6;
    const faderKnobHeight = size * 1.2;

    return (
      <View
        style={{
          marginLeft: orientation === 'PORTRAIT' ? margin : 20,
          marginBottom: orientation === 'PORTRAIT' ? margin * 0.72 : undefined,
        }}
      >
        <View
          style={{
            alignItems: 'center',
            justifyContent: 'center',
            shadowColor: '#000',
            shadowRadius: 10,
            shadowOpacity: 0.2,
            elevation: 10,
            width: size,
            top: orientation === 'LANDSCAPE' ? sliderTop : undefined,
            height: orientation === 'PORTRAIT' ? size : sliderHeight,
            borderRadius: orientation === 'PORTRAIT' ? size / 2 : undefined,
            marginBottom: 8,
          }}
        >
          <Canvas
            style={{ width: size, height: orientation === 'PORTRAIT' ? size : sliderHeight }}
            onTouch={touchHandler}
          >
            {orientation === 'PORTRAIT' ? (
              <Group origin={origin} transform={transform} opacity={isDisabled ? 0.5 : 1}>
                <Circle r={knobRadius} cx={size / 2} cy={size / 2} color={type === 'white' ? '#eee' : '#555'}>
                  <Path1DPathEffect
                    path="M -20 0 L 0 -10, 20 0, 0 30 Z"
                    advance={5}
                    phase={0}
                    style="rotate"
                  />
                  {/* <Shadow dx={0} dy={20} blur={2} color="rgba(0,0,0,0.2)" inner /> */}
                </Circle>
                <Circle
                  r={knobRadius - 2}
                  cx={size / 2}
                  cy={size / 2}
                  color={type === 'white' ? '#222' : '#eee'}
                  style="stroke"
                  strokeWidth={2}
                />
                <Rect
                  height={size / 2}
                  width={knobPointerWidth}
                  x={size / 2 - knobPointerWidth / 2}
                  y={0}
                  color={type === 'white' ? 'black' : 'white'}
                />
              </Group>
            ) : (
              <>
                <RoundedRect
                  x={size * 0.4}
                  y={size * 0.1}
                  width={size * 0.2}
                  height={screenHeight - size * 0.2 - insets.bottom}
                  color="#444"
                  opacity={isDisabled ? 0.5 : 1}
                />
                <Group transform={transformSlider}>
                  <RoundedRect
                    x={size * 0.2}
                    y={sliderHeight - size * 0.1 - faderKnobHeight}
                    width={faderKnobWidth}
                    height={faderKnobHeight}
                    r={4}
                    color={isDisabled ? '#222' : '#666'}
                  >
                    <Shadow dx={2} dy={2} blur={2} color="rgba(0,0,0,0.4)" />
                  </RoundedRect>
                </Group>
              </>
            )}
          </Canvas>
        </View>
        <Text
          style={{
            color: theme.colors.text,
            textAlign: 'center',
            textTransform: 'uppercase',
            maxWidth: size,
          }}
        >
          {label}
        </Text>
      </View>
    );
  }
);

export default Knob;
